import None from "@/components/qy/none.vue"
import tuiTabs from "@/components/tui-tabs/tui-tabs"
import tuiRate from "@/components/rate/rate"
export default {
	data() {
		return {
			currentTab: 0,
			navbar: [{
				name: "全部"
			}, {
				name: "有图"
			}],
			shua:true,
			list_empty: false,
			id:'',
			//rate_fen:this.$route.query.fen
			list:'',
			current: 5,
			index: 5,
			img:[require('@/imgs/8.jpg'),require('@/imgs/8.jpg'),require('@/imgs/8.jpg'),require('@/imgs/8.jpg'),
			require('@/imgs/8.jpg'),require('@/imgs/8.jpg'),require('@/imgs/8.jpg'),require('@/imgs/8.jpg'),require('@/imgs/8.jpg')]
		};
	},
	components: {tuiTabs,tuiRate,None},
	onLoad(options) {
		this.id = options.id
		this._load()
	},
	methods:{
		_load(){
			this.$api.http.get('product/get_evaluate?id=',{id:this.id}).then(res => {
				if(!res.data){
					this.list_empty=true
				}else{
					this.list = res.data
				}
				console.log(this.list)
			})
		},
		change(e){
			this.currentTab = e.index
			
		},
		changedd: function(e) {
			this.index = e.index;
			this.current = e.index
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.img,
				current: e.currentTarget.dataset.url
			});
		}
	},
	onPullDownRefresh() {
		this._load()
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
